@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page-link {
    margin: 10px 0;
    color: var(--the-white);
    background-color: var(--second-color);
    padding: 10px 0;
    border-radius: 20px;
    border: none;
    outline: none;
    cursor: pointer;
}

.page-link a {
    text-decoration: none;
    font-size: 14px;
    color: white;
}

.page-link:hover {
    background-color: var(--the-black);
}

.page-link a:hover {
    color: var(--the-white);
}
.menu-button {
    visibility: hidden;
    position: fixed;
    top: 15px;
    right: 10px;
    z-index: 2000;
    height: 40px;
    width: 40px;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    outline: none;
    background-color: var(--second-color);
}

.stripe {
    width: 24px;
    height: 1px;
    background-color: var(--the-white);
}

.exit-cross {
    color: var(--the-white);
}

@media only screen and (max-width: 1025px) {
    .menu-button {
        visibility: visible;
    }
}
::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}

.sidebar-container {
    height: 100vh;
    width: 32%;
    background-color: var(--the-white);
    margin: 0;
    /*-webkit-box-shadow: 10px 0px 22px -6px rgba(0,0,0,0.5);
    -moz-box-shadow: 10px 0px 22px -6px rgba(0,0,0,0.5);
    box-shadow: 10px 0px 22px -6px rgba(0,0,0,0.5);*/
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    overflow-y: scroll;
}

.close-button {
    position: fixed;
    top: 20px;
    right: 15px;
    cursor: pointer;
    display: none;
}

.sidebar-scroll {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: auto;
    margin-top: 10px;
}

.sidebar-titles h1, h2{
    font-weight: 100;
}

.sidebar-titles h1 {
    font-size: 2vw;
}

.sidebar-titles h2 {
    font-size: 1.5vw;
}

.sidebar-titles h3 {
    margin: 0;
}

.sidebar-titles {
    text-align: center;
    margin-top: 20px;
}

.stage-text {
    margin-bottom: 10px;
}

.progress-bar {
    width: 100%;
    margin: 10px 0 20px 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.progression {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.sidebar-items {
    width: 60%;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: flex-start;
}

.linked {
    color: var(--the-white);
    text-decoration: none;
    font-size: 1.4vw;
}

.linked:hover {
    color: var(--main-color);

}

.fine-print {
    margin: 20px 0 100px 0;
    font-size: 12px;
}

@media only screen and (max-width: 1025px) {
    .sidebar-container {
        width: 100%;
        z-index: 800;
        height: 100vh;
        display: none;
    }

    .sidebar-titles h1 {
        font-size: 40px;
    }
    
    .sidebar-titles h2 {
        font-size: 20px;
    }

    .sidebar-items {
        text-align: center;
    }

    .linked {
        font-size: 20px;
    }
}

@media only screen and (min-width: 1025px) {
    .sidebar-container {
        display: inline;
    }
}

@media only screen and (max-width: 1025px) {
    .sidebar-titles h1 {
        font-size: 30px;
    }
    
    .sidebar-titles h2 {
        font-size: 20px;
    }

    .close-button {
        display: inline;
    }
}

@media only screen and (max-width: 600px) {
    .page-link {
        margin: 10px 0;
    }
}
.waking-up {
    position: absolute;
    z-index: 200;
    top: 0;
    left: 0;
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--see-through);
}

.waking-up div {
    margin: 20px 0;
}

.loading-icon {
    height: 100px;
    width: 100px;
}

.first-circle {
    width: 100px;
    margin: 0;
    padding: 0;
    height: 100px;
    border-radius: 50%;
    border: 5px solid var(--the-black);
    border-top: 5px solid #248ec2;
    animation-name: spin;
    animation-duration: 3s;
    animation-iteration-count: infinite;
}

@keyframes spin {
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
}

.loading-text {
    font-size: 20px;
    padding: 20px;
    text-align: center;
}
.appointment-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    overflow-y: scroll;
    padding-bottom: 100px;
}

.mobile-title {
    display: none;
}

.adaptable-title {
    font-size: 26px;
    margin: 40px 0 40px 0;
    width: 100%;
}

.appointment-card {
    text-align: left;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: var(--the-white);
    cursor: pointer;
    transition: .4s;
    border-radius: 10px;
    border: 1px solid var(--the-black);
    width: 45%;
    margin-bottom: 40px;
    transition: 0.4s;
    font-family: inherit;
    color: var(--the-black);
    box-shadow: 0px 17px 13px -12px rgba(0,0,0,0.4);
}

.appointment-card:hover {
    border: 1px solid var(--the-white);
    box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.2);
}

.card-section {
    padding: 0 10px;
    margin: 5px;
}

.card-hr {
    text-align: left;
    width: 100%;
}

@media only screen and (max-width: 1025px) {
    .mobile-title {
        display: inline;
    }

    .adaptable-title {
        font-size: 20px;
        width: 100%;
        margin: 0 0 40px 0;
    }
}

@media only screen and (max-width: 600px) {
    .appointment-card {
        width: 90%;
        margin-bottom: 40px;
    }
}
.fu-page {
    width: 100%;
    text-align: center;
    overflow-y: scroll;
    padding-bottom: 100px;
}

.fu-border {
    width: 70%;
    max-width: 750px;
    padding-bottom: 40px;
    margin: 60px auto;
    border: 1px solid var(--the-black);
    border-radius: 10px;
}

.fu-container {
    width: 90%;
    margin: auto;
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
}

#fu-title {
    margin-top: 20px;
}

.submit-fu {
    margin: 40px 0;
    cursor: pointer;
    width: 100%;
    border-radius: 5px;
    background-color: var(--second-color);
    color: white;
}

@media (hover:hover) {
    .submit-fu:hover {
        background-color: var(--the-black);
        color: white;
    }
}

@media only screen and (max-width: 600px) {
    .fu-border {
        margin-top: 10px;
        width: 98%;
    }
  }
.searching-container {
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.searching-circle {
  width: 100px;
  margin: 0;
  padding: 0;
  height: 100px;
  border-radius: 50%;
  border: 5px solid var(--the-black);
  border-top: 5px solid #248ec2;
  animation-name: spin;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dates-container {
    width: 100%;
    text-align: center;
    overflow-y: scroll;
    padding-bottom: 100px;
}

.dates {
    text-align: center;
    margin: 20px 0;
    padding: 20px 0;
    border: 1px solid var(--the-black);
    border-radius: 10px;
    width: 80%;
    max-width: 700px;
    margin: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    transition: .4s;
}

.span-header {
    width: 90%;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.date-range {
    font-size: 16px;
}

.inc-button {
    height: 40px;
    width: 40px;
    cursor: pointer;
    border: 1px solid var(--the-black);
    border-radius: 5px;
    transition: .4s;
    background-color: white;
}

@media (hover:hover) {
    .inc-button:hover {
        background-color: var(--the-black);
        color: white;
    }
}

.date-headers {
    width: calc(100% / 6);
}

.date-axis {
    font-size: 16px;
}

.times-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-bottom: 20px;
    width: 100%;
}

.times {
    width: 90%;
    margin: 5px 0;
    padding: 5px 0;
    cursor: pointer;
    color: white;
    border-radius: 20px;
    border: none;
    background-color: var(--second-color);
    font-size: 12px;
}

@media (hover:hover) {
    .times:hover {
        background-color: var(--the-black);
    }
}

@media only screen and (max-width: 600px) {
    .dates {
        width: 96%;
    }

    .inc-button {
        width: 30px;
        height: 30px;
        font-size: 10px;
        padding: 0;
    }

    .times {
        font-size: 10px;
    }

    .date-range {
        font-size: 13px;
    }

    .date-axis {
        font-size: 10px;
    }
}
.books {
    width: 100%;
    text-align: center;
    overflow-y: scroll;
    padding-bottom: 100px;
}

.bookingContainer {
    width: 60%;
    padding: 0 0 20px 0;
    margin: 40px auto 10px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    border: 1px solid var(--the-black);
    border-radius: 10px;
}

.divider {
    border: 1px solid var(--second-color);
    width: 90%;
    margin: 20px auto;
}

.atBanner h2 {
    color: var(--the-black);
}

.atBanner p {
    margin-bottom: 0;
}

.hr {
    width: 90%;
    height: 1px;
    margin: 20px auto;
    background-color: var(--the-black);
    border: none;
}

.bookingForm {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}

.bookingForm input, select {
    width: 90%;
    margin: 10px auto;
    padding: 10px;
    border: none;
    -webkit-appearance: none;
}

.bookingForm select {
    background-color: white;
}

.bookingForm input[type='date'] {
    display:block;
     -webkit-appearance: textfield;
    -moz-appearance: textfield;
    min-height: 1.2em; 
    background-color: white;
}

.submitButton {
    width: 90%;
    margin: 10px auto;
    padding: 10px;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 5px;
    border: 1px solid var(--the-black);
    background-color: var(--second-color);
    color: white;
    -webkit-appearance: none;
    outline: none;
}

.submitButton:hover {
    background-color: var(--the-black);
    color: var(--the-white);
}

.bookingForm label {
    width: 100%;
    font-size: 14px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.bookingLabel {
    width: 45%;
    text-align: left;
    margin-left: 10px;
}

.requiredIcon {
    width: 45%;
    text-align: right;
    color: red;
}

.textArea {
    width: 90%;
    margin: 10px auto;
    padding: 10px;

}

.checkbox-alignment {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    text-align: left;
}

.paypal-container {
    width: 70%;
    margin: 30px auto;
}

@media only screen and (max-width: 600px) {
    .bookingContainer {
        width: 98%;
        margin: 10px auto;
    }

    .paypal-container {
        width: 90%;
        margin: 10px auto;
    }
}
.fu-pay-container {
    width: 100%;
    text-align: center;
    overflow-y: scroll;
    padding-bottom: 100px;
}

.fu-pay {
    margin: 100px 0;
    width: 70%;
    margin: auto;
    border: 1px solid var(--the-black);
    border-radius: 10px;
}

.fu-pp-container {
    width: 70%;
    margin: auto;
}

.fu-email-label {
    width: 90%;
    margin: 20px auto;
    text-align: left;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.fu-email {
    width: 100%;
    margin: 10px 0;
    padding: 10px 5px;
    border: none;
}

.fu-label {
    width: 45%;
    text-align: left;
}

.requiredIcon {
    width: 45%;
    text-align: right;
    color: red;
}

.fu-submit {
    width: 90%;
    border: none;
    border-radius: 5px;
    margin: 0 0 20px;
    padding: 10px 0;
    background-color: var(--second-color);
    color: white;
    cursor: pointer;
    -webkit-appearance: none;
    outline: none;
    transition: .2s;
}

@media (hover:hover) {
    .fu-submit:hover {
        background-color: var(--the-black);
    }
}

.fu-pay-in-clinic {
    width: 90%;
    padding: 10px 0;
    margin: 20px 0;
    border-radius: 5px;
    background-color: var(--second-color);
    border: none;
    color: white;
    cursor: pointer;
    transition: .2s;
}

@media (hover:hover) {
    .fu-pay-in-clinic:hover {
        background-color: var(--the-black);
    }
}

@media only screen and (max-width: 600px) {
    .fu-pay {
        width: 96%;
    }

    .fu-pp-container {
        width: 96%;
        margin: auto;
    }
  }
.completion-container {
    border: 1px solid var(--the-black);
    padding: 40px;
    width: 60%;
    margin: 140px;
}
:root {
  --the-black: #1d355e;
  --second-color: #248ec2;
  --the-white: #ffffff;
  --transparent: #ffffffc9;
  --see-through: #ffffff00;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.App {
  color: #1d355e;
  color: var(--the-black);
  font-family: 'Playfair Display', serif;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

hr {
  width: 80%;
  background-color: #1d355e;
  background-color: var(--the-black);
}

h1 {
  font-weight: 100;
}

h2 {
  font-size: 18px;
}

h3 {
  font-size: 16px;
  font-weight: 100;
}

h4 {
  font-size: 14px;
}

h5 {
  font-size: 12px;
}

a {
  color: #1d355e;
  color: var(--the-black);
}

a:hover {
  color: #248ec2;
  color: var(--second-color);
}

.portal {
  width: 68%;
  position: fixed;
  z-index: -1;
  top: 0;
  right: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  /*overflow-y: scroll;*/
  height: 100vh;
  background-image: url(/static/media/background-image.287f3f61.jpg);
  background-image: -webkit-image-set(url(/static/media/background-image.0ce4ec80.webp) 1x);
  background-position: center;
  background-size: cover;
}

.portal-title {
  margin: 60px 0 20px 0;
  width: 100%;
}

.back-to-calendar {
  width: 60%;
  border: none;
  background-color: #1d355e;
  background-color: var(--the-black);
  padding: 10px 0;
  margin: 10px 0 20px 0;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  transition: 0.2s;
}

/*.back-to-calendar:hover {
  background-color: white;
  color: var(--the-black);
}*/

@media only screen and (max-width: 1025px) {
  .portal {
      width: 100%;
  }

  h3 {
    font-size: 14px;
  }

  .portal-title {
    font-size: 24px;
  }
}

@media only screen and (max-width: 600px) {
  h2 {
    font-size: 14px;
  }

  h3 {
    font-size: 12px;
  }

  h4 {
      font-size: 12px;
  }

  h5 {
    font-size: 10px;
  }

  p {
    font-size: 12px;
  }

  .back-to-calendar {
    width: 90%;
  }
}

@media (hover:hover) {
  .back-to-calendar:hover {
    background-color: white;
    color: #1d355e;
    color: var(--the-black);
  }
}
